.alert-table {
  margin: 0 auto;
  padding-bottom: 2rem; }
  @media (max-width: 767px) {
    .alert-table {
      width: 100%; } }
  .alert-table .alert-item {
    padding-top: 1rem;
    padding-bottom: 1rem;
    background: #dee2e6; }
    .alert-table .alert-item .alert-title {
      margin-left: 1rem; }
    .alert-table .alert-item .alerted-date {
      margin-left: 2rem; }
    .alert-table .alert-item .writer {
      margin-left: 3rem; }
  .alert-table .alert-item + .alert-item {
    padding-bottom: 1rem;
    padding-top: 1rem;
    border-top: 1px solid black; }



